import { AddEmitterDialogComponent } from './add-emitter-dialog/add-emitter-dialog.component';
import { Emitter } from '../@core/models/emitter.model';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { MatDialog } from '@angular/material/dialog';
import { ConfigurationService } from '../@core/services';
import { MatTableDataSource } from '@angular/material/table';


@Component({
  selector: 'app-emitters-page',
  templateUrl: './emitters.component.html',
  styleUrls: ['./emitters.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0px', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ])
  ]
})

export class EmittersComponent implements OnInit {
  emitterList = new MatTableDataSource<Emitter>([]);
  columnsToDisplay = ['type', 'active', 'collectMetrics'];
  columnsToDisplayAndActions = ['type', 'active', 'collectMetrics', 'actions'];
  expandedElement: Emitter | null;

  constructor(
    public dialog: MatDialog,
    private configurationService: ConfigurationService,
    private changeDetectorRefs: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.fetchEmitters();
  }

  fetchEmitters() {
    const emitters = this.configurationService.getEmmiters();
    this.emitterList.data = emitters;
    this.changeDetectorRefs.detectChanges();
  }

  addNewOpenDialog() {
    const dialogRef = this.dialog.open(AddEmitterDialogComponent, {
      id: 'AddEmitterDialogComponent'
    });

    dialogRef.afterClosed().subscribe(emitter => {
      if (emitter) { this.addNewEmitter(emitter); }
    });
  }

  addNewEmitter(emitter: Emitter) {
    this.configurationService.addEmitter(emitter);
    this.fetchEmitters();
  }

  removeEmitter(emitter: any) {
    const index = this.emitterList.data.indexOf(emitter);
    this.configurationService.removeEmitter(index);
    this.fetchEmitters();
  }

}
