
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { FormArray, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatStepper } from '@angular/material/stepper';
import { MatSelectChange } from '@angular/material/select';
import { ConfigurationService } from './../../@core/services/configuration';

@Component({
  selector: 'app-add-emitter-dialog',
  templateUrl: './add-emitter-dialog.component.html'
})
export class AddEmitterDialogComponent implements OnInit {
  isLinear = true;
  emitterTypeFormGroup: FormGroup;
  propertiesFormGroup: FormGroup;
  otherPropertiesFieldNames: string[] = [];
  otherFields: any = [];

  emitterTypes = [
    'pcap',
    'socket'
  ];

  @ViewChild('stepper') private stepper: MatStepper;


  constructor(
    private _formBuilder: FormBuilder,
    private configurationService: ConfigurationService,
    private dialogRef: MatDialogRef<AddEmitterDialogComponent>,
  ) { }


  ngOnInit() {
    this.emitterTypeFormGroup = this._formBuilder.group({
      emitterType: ['', Validators.required]
    });
    this.createPropertiesFormGroup([]);
  }

  createPropertiesFormGroup(fields: any) {
    const list: any = {
      active: [false],
      collectMetrics: [true]
    };
    this.otherFields = fields;
    fields.forEach((field: any) => {
      list[field.name] = [''];
    });
    this.propertiesFormGroup = this._formBuilder.group(list);
  }

  onEmitterTypeChange(event: MatSelectChange) {
    const val = event.value;
    const properties = this.configurationService.getEmitterProperties(val);
    this.createPropertiesFormGroup(properties?.fields);
  }

  submit() {
    const values = {...this.propertiesFormGroup.value};
    values.type = this.emitterTypeFormGroup.value.emitterType;
    this.dialogRef.close(values);
  }


}
