import { ConfigurationService } from './../@core/services/configuration';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-global-page',
  templateUrl: './global.component.html',
  styleUrls: ['./global.component.scss']
})
export class GlobalComponent implements OnInit {
  form: FormGroup;

  constructor(
    private _formBuilder: FormBuilder,
    private configurationService: ConfigurationService
  ) {
    this.form = _formBuilder.group({
      sessionMapper: ''
    });
  }

  ngOnInit() {
    const sessionMapper = this.configurationService.getSessionMapper();
    this.form.patchValue({ sessionMapper: sessionMapper });
  }

  public save() {
    const sessionMapper = this.form.value.sessionMapper;
    this.configurationService.saveSessionMapper(sessionMapper);
  }

  public onSubmit(e: Event) {
    this.save();
    e.preventDefault();
  }
}

