import { Injectable } from '@angular/core';

const LOCAL_STORAGE_KEY = 'configuration';

@Injectable()
export class ConfigurationStorageService {

  getConfiguration(): String {
    return window.localStorage[LOCAL_STORAGE_KEY];
  }

  saveConfiguration(configuration: String) {
    window.localStorage[LOCAL_STORAGE_KEY] = configuration;
  }

  destroyConfiguration() {
    window.localStorage.removeItem(LOCAL_STORAGE_KEY);
  }
}
