import { Emitter } from './../models/emitter.model';
import { Configuration } from './../models/configuration.model';
import { Injectable } from '@angular/core';

import { ConfigurationStorageService } from './storage.service';
import EMITTERS_PROPERTIES from '../../@mock/emitters-properties.json';

const initialConfiguration: Configuration = {
  emitter: {
    sessionMapper: '',
    emitters: []
  }
};

@Injectable()
export class ConfigurationService {
  configuration: Configuration;
  constructor(private storageService: ConfigurationStorageService) { }

  getConfiguration(): Configuration {
    if (!this.configuration) {
      const configurationString = this.storageService.getConfiguration() as string;
      let configuration;
      if (configurationString) {
        configuration = JSON.parse(configurationString) as Configuration;
      } else {
        configuration = initialConfiguration;
      }
      this.configuration = configuration;
    }
    return this.configuration;
  }

  saveConfiguration(): void {
    const configurationString = JSON.stringify(this.configuration) as String;
    this.storageService.saveConfiguration(configurationString);
  }

  getEmitterProperties(name: string) {
    return EMITTERS_PROPERTIES.find((emitter) => emitter.type === name);
  }

  getSessionMapper(): string {
    return this.getConfiguration().emitter.sessionMapper;
  }

  saveSessionMapper(sessionMapper: string) {
    this.configuration.emitter.sessionMapper = sessionMapper;
    this.saveConfiguration();
  }

  getEmmiters() {
    return this.getConfiguration().emitter.emitters;
  }

  addEmitter(emitter: Emitter) {
    this.configuration.emitter.emitters.push(emitter);
    this.saveConfiguration();
  }

  removeEmitter(index: number) {
    this.configuration.emitter.emitters.splice(index, 1);
    // delete this.configuration.emitter.emitters[index];
    this.saveConfiguration();
  }
}
